.header {
  max-width: 1600px;
  height: 216px;
  margin: 0 auto;
  // position: relative;
  // top: 0;
  // left: 0;
  padding-top: 40px;
  background-color: $buttonBgColor;
  background-image: $bg-header-gradient, url(../images/bg-home-mobile@1x-min.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @include retina {
    background-image: $bg-header-gradient,
      url(../images/bg-home-mobile@2x-min.jpg);
  }

  @include tablet {
    background-image: $bg-header-gradient,
      url(../images/bg-home-tablet@1x-min.jpg);

    @include retina {
      background-image: $bg-header-gradient,
        url(../images/bg-home-tablet@2x-min.jpg);
    }
  }

  @include desktop {
    background-image: $bg-header-gradient,
      url(../images/bg-home-desktop@1x-min.jpg);

    @include retina {
      background-image: $bg-header-gradient,
        url(../images/bg-home-desktop@2x-min.jpg);
    }
  }
}

.header__container {
  position: relative;
}

.header__wraper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 34px;
  position: relative;

  @include tablet {
    margin-bottom: 54px;
  }
}

.logo__site {
  display: flex;
  align-items: center;
  color: $mainLightColor;
  fill: $mainLightColor;
  transition: color $duration $timingFunction,
              transform $duration $timingFunction;

  &:hover,
  &:focus {
    transform: scale(1.25);
  }
}

.logo__site:hover .logo__name {
  color: $accentColor;
}

