.navigation-list {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 0;
}
.navigation-list__link {
  text-transform: uppercase;
  color: $mainLightColor;
  line-height: 1.667;
  transition: color $duration $timingFunction;
  &:hover {
    color: $accentColor;
    cursor: pointer;
    outline: none;
  }
}

.navigation-list__link-current::after {
  bottom: -3px;

  display: block;
  content: '';
  width: 100%;
  height: 3px;
  background-color: $accentColor;
}

.authorization {
  position: absolute;
  right: 0;
  bottom: -59px;
  display: flex;
  // transform: translateY(-100%);
  @include tablet {
    transform: translateY(0);
  }
}

.authorization__item {
  margin-left: 30px;
}

.authorization__btn {
  width: fit-content;
  display: flex;
  flex-wrap: nowrap;
  white-space: nowrap;
  align-items: center;
  margin: 0;
  padding: 5;
  line-height: 1.67;
  font-weight: 500;
  background-color: transparent;
  border: none;
  // cursor: pointer;
  text-transform: uppercase;
  color: $informationColor;
  fill: $informationColor;
  transition: transform $duration $timingFunction;
  // border-color: #ff001b;
  // border-style: ridge;
  // border-radius: 7px;
  font-size: 14px;
  opacity: 1;
  // transform: translateY(10%);
  &:hover {
    transform: scale(1.25);
  }
}

.authorization__btn.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.authorization__btn.is-visible {
  transform: translate(205%);

}

.navigation-list__item {
  font-weight: 500;
  line-height: 1.17px;
  // &:hover,
  // &:focus {
  //   transform: scale(1.25);
  //   transition: transform $duration $timingFunction;
  // }
  &:not(:last-child) {
    margin-right: 32px;
  }
}

.header__form {
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
}

.header__form-search {
  position: relative;
  margin-top: 35px;

  @include desktop {
    margin-top: 0;
  }
  
}

.header__input {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: $mainLightColor;
  width: 280px;
  position: relative;
  border-bottom: 0.5px solid $mainLightColor;
  border-top: none;
  border-left: none;
  border-right: none;
  outline: 0;
  background: transparent;

  @include tablet {
    width: 336px;
  }

  @include desktop {
    width: 394px;
  }
}

.header__input::placeholder {
  color: $mainLightColor;
}

.header__search-btn {
  stroke: $mainLightColor;
  fill: $mainLightColor;
  position: absolute;
  width: 30px;
  height: 30px;
  top: -8px;
  right: 0;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  &:hover,
  &:focus {
    cursor: pointer;
    transform: scale(1.25);
    transition: transform $duration $timingFunction;
  }
}

