@include tablet {
  .logo__name {
    display: block;
    color: $mainLightColor;
    font-weight: 500;
    font-size: 30px;
    line-height: 1.17;
  }

  .logo {
    display: flex;
    justify-content: center;
    align-items: baseline;
  }

  .logo__svg {
    margin-right: 10px;
  }

  .header__input {
    width: 336px;
  }

  .header__form-search {
    position: relative;
  }
}

@include desktop {
  .header__input {
    width: 394px;
  }
}

.header__input-error {
  display: flex;
  justify-content: center;
  color: $informationColor;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
}
